body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.login-form-wrapper{
    height: 41em;
    display: flex;
    justify-content: center;
    align-items: center;
}
.login-form-wrapper h1{
    margin: 45px 0px 0px 10px; 
    float: right;
}
.login-form-wrapper img{
    float: left;
}
.login-btn{
    float: right;
}


.inputWrapper{
    width: 100%;
}
/* styling of navbar */
.navbar{
    height: 4em;
    margin: 0px;
    margin-bottom: 5px;
}

.logout-btn i{
    font-size: 30px;
    color: white;
}
.dashboard{
    margin-top: 0px ;
    padding: 0px;
    padding-top: 0px;
    float: left;
    color: white;
}


.dashboard strong{
    font-size: 39px;    
    padding: 0px;
    margin: 0px
}

.dashboard i{
    padding-left: 10px; 
    padding-top: 5px;
    font-size: 50px;
}

.logout-btn button{
    font-size: 30px;
    color: white;
}
.logout-btn{
    padding-top: 10px;
    display: block;
    float: right;
}

/* end of navbar styling*/

/* start of side bar styling*/
.quiz-tree{
    height: 567px;
    padding: 0px;
    margin: 0px;
    float: left;
    color: white; 
}

.course-active{
    background-color: #0288d1; 
}

.quiz-tree-wrapper i{    
    margin-left: 0px;
    padding-left: 0px;  
    padding-top: 10px; 
    margin-top: 5px;
    padding: 0px;

}

.quiz-tree .row{
    padding: 0px;
    margin: 0px;
}

.quiz-tree-wrapper{
    padding: 0px;
    margin: 0px;    
}

.quiz-tree-courses{
    margin: 0px ; 
    font-size: 23px;
    padding: 0px;
    border-bottom: 2px solid white;

}

.quiz-tree-courses-drop li{
    display: block;
    padding: 4px 0px 0px 30px;
    margin: 0px;
    font-size: 20px;
    
}

.space-filler{
    height: 30px;
    width: 100%;    
    margin: 0px;
    padding: 0px;  
    float: left;
    display: flex;
    justify-content: center;
    align-items: center;
}
/* end of side bar styling*/

/* course detail diplay porting styling */
.details{
    display: flex;
    float: right;
    height: 597px;
}
/* course detail diplay porting styling end */

/*Course Detail styling*/

.course-detail h1{
    color: #0288d1; 
}

.right-aligner{
    width: 250px;
    text-align: left;
}

.course-detail h5{
    display: inline;    
}
.text-block{
    padding-left: 200px;

}
/*Course Detail styling end*/
